const form = document.querySelector("form");
const input = document.querySelector("input");


form.addEventListener("submit", (event) => {
    console.log(event);
    event.preventDefault();
});

input.addEventListener("keydown", (event) => {
    if(event.key === "t"){
        input.style.color="red"
    }
    console.log(event);
});


